<template>
    <el-card class="box-card">
        <div slot="header">
            <span>账户资料</span>
        </div>
        <el-form ref="form" :rules="rules" :model="form" label-width="80px">
            <el-form-item label="用户名">
                <!-- readonly 只读属性 -->
                <el-input v-model="form.username" readonly></el-input>
            </el-form-item>
            <el-form-item prop="fullname" label="姓名">
                <el-input v-model="form.fullname"></el-input>
            </el-form-item>
            <el-form-item label="性别">
                <el-radio-group v-model="form.sex">
                    <el-radio label="男"></el-radio>
                    <el-radio label="女"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item prop="tel" label="手机">
                <el-input v-model="form.tel"></el-input>
            </el-form-item>
            <el-form-item prop="email" label="邮箱">
                <el-input v-model="form.email"></el-input>
            </el-form-item>
            <el-form-item prop="avatar" label="头像">
                <ImgUpload v-model="form.avatar" :data="{ type: 'avatar' }"></ImgUpload>
            </el-form-item>
            <el-button @click="handleEdit" type="primary">保存修改</el-button>
        </el-form>
    </el-card>
</template>

<script>
// Role 请求 角色 时用
import ImgUpload from '@/components/Img-upload.vue'
import { mapState, mapActions } from 'vuex';

export default {
    name: 'Info',
    components: {
        ImgUpload,
    },
    data() {
        return {
            form: {
                user_id: '',
                username: '',
                fullname: '',
                sex: '',
                role_id: '',
                tel: '',
                email: '',
                avatar: '',
            },
            // 表单验证规则
            rules: {
                fullname: [
                    { required: true, message: '请输入姓名！', trigger: 'blur' },
                ],
                tel: [
                    { required: true, message: '请输入手机号！', trigger: 'blur' },
                    { pattern: /^1((3|5|8){1}\d{1}|70)\d{8}$/, message: '请正确输入手机号！', trigger: 'blur' }
                ],
                email: [
                    { required: true, message: '请输入邮箱！', trigger: 'blur' },
                    { pattern: /^[a-zA-Z0-9][a-zA-Z0-9_]+\@[a-zA-Z0-9]+\.(com|cn|net|com.cn)$/i, message: '请正确输入邮箱！', trigger: 'blur' }
                ],
            }
        }
    },
    computed: {
        ...mapState('user', ['profile']),
    },
    created() {
        // 还原数据
        this.form = { ...this.profile };
    },
    methods: {
        ...mapActions('user', ['edit_profile']),
        // 修改账户信息
        async handleEdit() {
            // 表单验证的第二种写法 ==> 参考 Element UI ==> validate中不传入参数(即回调函数)时
            // 则会返回一个 promise ==> 可使用 try catch 捕获成功与失败 
            try {
                let valid = await this.$refs.form.validate();
                if (valid) {
                    let { status, msg } = await this.edit_profile({ ...this.form });
                    if (status) {
                        // 信息提示
                        this.$message.success(msg);
                    } else {
                        this.$message.error(msg);
                    }
                }
            } catch (error) {
                this.$message.info(error);
            }
        },
    },
}
</script>

<style lang="less" scoped>
.avatar-uploader .el-upload {
    border: 2px solid #111010;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}

.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
}

.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
}

.avatar {
    width: 178px;
    height: 178px;
    display: block;
}
</style>
